

/* DiarioTrader.css */
.diario-trader-container {
  max-width: 1200px; /* Largura máxima do container do formulário */
  margin: auto; /* Centraliza o container */
  padding: 20px;
  background-color: #f9f9f9; /* Fundo claro para todo o formulário */
}

.diario-header {
  font-size: 1.75rem; /* Tamanho do título */
  color: #333; /* Cor do texto */
  margin-bottom: 20px; /* Espaço abaixo do cabeçalho */
  text-align: center; /* Centraliza o texto */
  padding: 10px 0; /* Padding superior e inferior para o cabeçalho */
  background-color: #ffffff; /* Fundo branco para o cabeçalho */
  border-bottom: 2px solid #dee2e6; /* Linha de separação abaixo do cabeçalho */
}

.form-label {
  display: block; /* Garante que o label ocupe a própria linha */
  margin-bottom: 0.5rem; /* Espaço abaixo do label */
  color: #495057; /* Cor escura para fácil leitura */
  font-weight: bold; /* Torna o texto do label negrito */
}

.form-control {
  display: block;
  width: 100%; /* Ocupa toda a largura disponível da coluna */
  padding: 0.375rem 0.75rem; /* Padding interno para os campos de entrada */
  font-size: 1rem; /* Tamanho do texto padrão */
  line-height: 1.5; /* Altura da linha */
  color: #495057; /* Cor do texto */
  background-color: #fff; /* Fundo branco para os campos de entrada */
  background-clip: padding-box;
  border: 1px solid #ced4da; /* Borda sutil */
  border-radius: 0.25rem; /* Bordas arredondadas */
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-control:focus {
  border-color: #80bdff; /* Cor da borda ao focar no campo */
  outline: 0; /* Remove o outline padrão */
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25); /* Sombra suave ao focar */
}

.btn-primary {
  color: #fff; /* Texto branco */
  background-color: #007bff; /* Fundo azul */
  border-color: #007bff; /* Borda azul */
}

.btn-primary:hover {
  color: #fff;
  background-color: #0069d9; /* Escurece o fundo azul ao passar o mouse */
  border-color: #0062cc;
}

/* Estilos para a navegação por guias (se estiver usando React Bootstrap Tabs) */
.nav-pills .nav-link {
  border-radius: 0.25rem;
}

.nav-pills .nav-link.active, .nav-pills .nav-link:hover {
  color: #fff;
  background-color: #007bff; /* Fundo azul para o item ativo ou ao passar o mouse */
}

/* Estilos responsivos */
@media (max-width: 768px) {
  .diario-trader-container {
    padding: 10px;
  }

  .form-control {
    padding: 0.25rem 0.5rem; /* Padding menor para dispositivos móveis */
  }

  .btn-primary {
    padding: 0.375rem 0.75rem; /* Padding padrão para botões em dispositivos móveis */
  }
}
