.menu-container {
  width: 100%;
  background-color: #f8f8f8; /* Cor de fundo do menu */
}

.menu-nav {
  display: flex;
  flex-direction: column;
}

.menu-list {
  list-style-type: none;
  padding: 0;
}

.menu-list li {
  padding: 10px 20px;
  border-bottom: 1px solid #ddd; /* Linha separadora dos itens */
}

.menu-list li a {
  text-decoration: none;
  color: #333; /* Cor do texto */
  display: block;
}

.menu-list li a.active {
  background-color: #e7e7e7; /* Cor de fundo do item ativo */
}
